import React, { useEffect, useState } from 'react';
import { useSelector, useDispatch } from "react-redux"
import { actions } from '../redux/_actions';
import Layout from './layout';
import api from '../api';

const Profile = () => {
  const [sendStatus, setSendStatus] = useState(false);
  const [secureUid, setSecureUid] = useState('');
  const [interv, setInterv] = useState(null);

  const dispatch = useDispatch();
  const { email, secure } = useSelector(state => state.auth);

  useEffect(() => {
    if (sendStatus && !interv) {
      const checkSecureVerified = () => {
        api.checkSecureVerified(secureUid).then((data) => {
          if (!data || data.error) {
            console.log(data && data.error);
          } else {
            if(data.success) {
              clearInterval(interv);
              setInterv(null);
              setSendStatus(false);
              dispatch(actions.setSecure(1));
            }
          }
        });
      }

      const interval = setInterval(checkSecureVerified, 5000);
      setInterv(interval)
    }
  }, [sendStatus])

  const setSecure = () => {
    dispatch(actions.setLoading(true));
    api.setSecure().then((data) => {
      dispatch(actions.setLoading(false));
      if (!data || data.error) {
        console.log(data && data.error);
      } else {
        console.log(data)
        setSecureUid(data.uid)
        setSendStatus(true);
      }
    });
  }

  const signout = () => {
    dispatch(actions.signout())
  }

  return (
    <Layout>
      <div className='profile-page'>
        {/* <div className='avatar'>
          <img alt="profile"/>
        </div>
        <div className='input-label small'>Select your avatar by clicking the area</div> */}
        <div className='input-label'>{email}</div>
        {!secure && !sendStatus && <button className="input-button" onClick={setSecure}>
          Security
        </button>}
        {!secure && sendStatus && <div className="input-label">Verification email sent</div>}
        {secure && <div className="input-label">Your account requires email verification to login</div>}
        <button className="input-button" onClick={signout}>
          Sign Out
        </button>
      </div>
    </Layout>
  )
}

export default Profile;
